@import "./modules/vars";
@import "./modules/mixins";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;

  @include md {
    font-size: 100%;
  }
  @include lg {
    font-size: 100%;
  }
}

body {
  background-color: $bg;
  font-family: "Roboto", sans-serif;
  position: relative;
  overflow-x: hidden;
}

