//Preloader style
/* Variables */
/* Mixins */
/* Animations */
@-webkit-keyframes preloader-inside-white {
  0% {
    transform: scale(0, 0);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes preloader-inside-white {
  0% {
    transform: scale(0, 0);
  }
  100% {
    transform: scale(1, 1);
  }
}
@-webkit-keyframes preloader-inside-red {
  0% {
    transform: scale(0, 0);
  }
  30% {
    transform: scale(0, 0);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes preloader-inside-red {
  0% {
    transform: scale(0, 0);
  }
  30% {
    transform: scale(0, 0);
  }
  100% {
    transform: scale(1, 1);
  }
}
/* Styles */
.preloader {
  display: inline-block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background: #383957;
  text-align: center;
}
.preloader .preloader-container {
  display: inline-block;
  width: 100px;
  height: 100px;
  margin: auto;
  position: absolute;
  top: 44%;
  left: 0;
  position: relative;
}
.preloader .preloader-container .animated-preloader {
  display: inline-block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  background: #00bb81;
  border-radius: 50em;
}
.preloader .preloader-container .animated-preloader:after {
  content: "";
  display: inline-block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50em;
  background: lighten(#00bb81, 5%);
  animation: preloader-inside-white 1s ease-in-out infinite;
}
.preloader .preloader-container .animated-preloader:before {
  content: "";
  display: inline-block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  border-radius: 50em;
  background: lighten(#00bb81, 15%);
  animation: preloader-inside-red 1s ease-in-out infinite;
}

header {
  height: 60px;
}
.navbar-brand {
  padding-left: 12px;
  img {
    width: 32px;
  }
  h3 {
    font-family: "Concert one", sans-serif;
    font-size: 24px;
    color: white;
    letter-spacing: 1px;
    margin: 0;
    margin-left: 10px;
  }
}

.nav-toggler {
  width: 22px;
  height: 18px;
  position: relative;
  cursor: pointer;
  display: inherit;
  transition: all 0.4s ease;
  margin-right: 15px;
  @include xl {
    display: none;
  }
  span {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #00bb81;
    transition: all 0.4s ease;
    top: 0;
  }

  span:nth-child(2) {
    top: 50%;
  }

  span:nth-child(3) {
    top: 100%;
  }
}

nav {
  position: relative;
  ul {
    margin-bottom: 0;
    list-style-type: none;
    position: absolute;
    right: 0;
    transform: scaleY(0);
    flex-direction: column;
    background-color: lighten(#383957, 3%);
    padding: 25px;
    border-radius: 5px;
    position: absolute;
    right: 0;
    z-index: 2;
    margin-top: 80px;
    display: flex;
    transition: all 0.5s ease;
    transform-origin: top;

    @include xl {
      position: relative;
      display: flex;
      flex-direction: row;
      transform: translateY(0);
      margin-top: 0;
      background-color: inherit;
    }
    li {
      margin-left: 0;
      margin-top: 12px;

      @include xl {
        margin-left: 30px;
        margin-top: 0;
      }

      img {
        width: 18px;
        margin-right: 5px;
      }
    }

    li:first-child {
      margin-top: 0;
      a {
        color: #eb5569;
      }
    }

    li:last-child {
      a {
        color: #00bb81;
      }
    }
    a {
      text-decoration: none;
      font-size: 14px;
      transition: all 0.3s ease;
    }
    a:hover {
      pointer-events: none;
      cursor: default;
      text-decoration: none;
    }
  }
}

.nav-open {
  .nav-toggler {
    span {
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.nav-open {
  position: relative;

  nav {
    position: relative;
  }
  .nav-wrap {
    transform: scaleY(1);
  }
}

.landing-stat {
  margin-top: 15px;

  .stat-inr {
    background: linear-gradient(
      0deg,
      lighten($bg, 2%),
      lighten($bg, 3%),
      lighten($bg, 4%)
    );
    padding: 30px;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
  }

  .btn {
    border: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    word-spacing: 3px;
    font-size: 14px;
    width: 100%;
    position: relative;
    z-index: 1;
    transition: all 0.5s ease;
    background-color: transparent;
    border: 1px solid #007bff;
    margin-top: 36px;

    svg {
      margin-top: -3px;
      margin-right: 5px;
    }
  }
}

.landing-title {
  font-size: 24px;
  color: white;
  font-weight: 500;
  padding-bottom: 15px;
  position: relative;
  z-index: 1;

  svg {
    color: #515585;
    margin-top: -5px;
    margin-right: 5px;
  }
}

.counter-up {
  padding-top: 20px;
  padding-bottom: 20px;
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 6px;
  grid-row-gap: 6px;
  position: relative;
  z-index: 1;

  @include md {
    grid-template-columns: auto auto auto;
  }
  @include xl {
    grid-template-columns: auto auto auto auto;
  }
}

.counter-single {
  background-color: darken($bg, 1%);
  padding: 15px;
  border-radius: 3px;

  h4 {
    color: #dbd8e3;
    font-weight: 600;
    font-size: 1.1rem;
  }
  h5 {
    font-size: 0.6rem;
    margin-bottom: 0;
    color: lighten(#515585, 10%);
  }
}

.country-select {
  margin-top: 0 !important;
  position: relative;
  margin-top: 25px;
  position: relative;
  z-index: 1;

  .select-wrap {
    position: relative;
    width: 100%;
    height: fit-content;
    svg {
      color: #515585;
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%);
      pointer-events: none;
    }
    select {
      width: 100%;
      background-color: darken($bg, 1%);
      border: none;
      border-radius: 5px;
      padding: 8px;
      padding-left: 12px;
      font-size: 16px;
      color: #515585;
      -webkit-appearance: none;
      -moz-appearance: none;
      cursor: pointer;
    }

    select:focus {
      outline: none;
    }
  }
  label {
    color: #515585;
    font-size: 14px;
    text-align: left;
    text-align: center;
  }
}
